var INK_API_VERSION = 'v1';

// module.exports = {
//   apiBaseUrl : process.env.NODE_ENV == 'development' ?  'http://localhost:8080' : 'http://ink-api.coko.foundation',
//   apiVersionHeader : "application/vnd.ink.v1"
